@import 'reset';
@import 'fonts';
@import 'var';

header{
	position: relative;
}

.content-wrapper{
  max-width: 940px; 
  /* max-width: 63%; */ 
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
}

.head-block{
  background-image: url(../img/head-block/headbg.png);
  height: 1057px;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
}
.head-block__logo{
     width: 145px;
    height: 75px;
    background-color: #fa5c65;
    display: inline-block;
    border-radius: 0 0 10px 10px;
    color: #fff;
    font-family: Domine;
    font-size: 20px;
    font-weight: 700;
    letter-spacing: .3px;
    text-align: center;
